export default {
	data() {
		return {
			con:[]
		}
	},
	methods: {
		goIndex(){
			uni.switchTab({
				url:"/pages/tabBar/home"
			})
		},
		evaluate(){
			uni.navigateTo({
				url:"/pages/user/keep/sayFeel/sayFeel?eval="+this.con
			})
		}
	},
	onLoad(e) {
		this.con=e.con
	}
}
